import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Container} from "react-bootstrap";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faGithub, faVk} from "@fortawesome/free-brands-svg-icons";
import {faAt, faHome} from "@fortawesome/free-solid-svg-icons";

class Contacts extends React.Component {
    render() {
        return (
            <div id="contacts" className="text-light bg-dark ">
                <Container className="pt-5 pb-3 text-center">
                    <h3 className="pt-2 pb-2">Контакты</h3>
                    <div style={{fontSize: '1rem'}}>
                        <FontAwesomeIcon id="" className="hover align-middle" icon={faVk} fixedWidth size="lg"/>
                        <p className="d-inline-block align-super mb-1">
                        <a href="https://vk.com/panzerkampfwagen6tiger" target="_blank" rel="noopener noreferrer">Денис Купцов</a>
                        </p>
                        <br/>
                        <FontAwesomeIcon id="" className="hover align-middle" icon={faAt} fixedWidth size="lg"/>
                        <p className="d-inline-block align-super mb-1">wanetols@gmail.com</p>
                        <br/>
                        <FontAwesomeIcon id="" className="hover align-middle" icon={faHome} fixedWidth size="lg"/>
                        <p className="d-inline-block align-super mb-1">Г. Москва</p>
                        <br/>
                        <FontAwesomeIcon id="" className="hover align-middle" icon={faGithub} fixedWidth size="lg"/>
                        <p className="d-inline-block align-super mb-1">
                        <a href="https://github.com/WaneTol72" target="_blank" rel="noopener noreferrer">WaneTol72</a>
                        </p>
                    </div>
                </Container>
            </div>
        )
    }
}
export default Contacts;