import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Carousel, Image} from "react-bootstrap";
import nixsv from "../Asset/nixsv.webp";
import forest2 from "../Asset/forest2.webp";

class Examples extends React.Component{
    render() {
    return (
    <div id="examples" className="text-light bg-dark">
        <div className="w-75 m-auto pt-5 pb-5">
            <h2 className="text-center pt-2 pb-2">Примеры работ</h2>
            <Carousel>
                <Carousel.Item>
                    <Image
                        alt="screensite"
                        className="d-block w-100"
                        src={nixsv}
                        rounded
                    />
                    <Carousel.Caption>
                        <h3><a href="https://wanetol72.github.io/nixsvru/" target="_blank" rel="noopener noreferrer">nixsv.ru</a></h3>
                        <p>Сайт игрового проекта</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <Image
                        alt="forest"
                        className="d-block w-100"
                        src={forest2}
                        rounded
                    />
                    <Carousel.Caption>
                        <h3>Пусто</h3>
                        <p>Второго ещё нет</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </div>
    </div>
    )
    }
}
export default Examples;